import { CompositeDisposable } from 'atom';
import { CREditorControl } from './editor-control';
export class CheckResultsProvider {
    constructor(pluginManager) {
        this.pluginManager = pluginManager;
        this.tooltipProvider = (editor, crange, type) => {
            const controller = this.pluginManager.controllerType(CREditorControl, editor);
            if (!controller) {
                return undefined;
            }
            if (type === "keyboard" &&
                atom.config.get('ide-haskell.onCursorMove', {
                    scope: editor.getRootScopeDescriptor(),
                }) !== 'Show Tooltip') {
                return undefined;
            }
            const msg = Array.from(controller.getMessageAt(crange.start, type));
            const actions = () => controller.getActionAt(crange.start, type);
            if (msg.length > 0) {
                return { range: crange, text: msg, actions };
            }
            return undefined;
        };
        const tooltipRegistry = pluginManager.tooltipRegistry;
        this.disposables = new CompositeDisposable();
        this.disposables.add(tooltipRegistry.register('builtin:check-results', {
            priority: 1000,
            handler: this.tooltipProvider,
            eventTypes: ["mouse", "keyboard"],
        }), pluginManager.addEditorController(CREditorControl));
    }
    destroy() {
        this.disposables.dispose();
    }
}
