import { Button } from './output-panel-button';
import * as etch from 'etch';
export class OutputPanelButtons {
    constructor(props) {
        this.props = props;
        etch.initialize(this);
    }
    render() {
        return (etch.dom("ide-haskell-panel-buttons", null, Array.from(this.renderButtons())));
    }
    async update(props) {
        this.props = props;
        return etch.update(this);
    }
    *renderButtons() {
        for (const props of this.props.buttons) {
            yield (etch.dom(Button, { active: props.name === this.props.activeBtn, name: props.name, count: props.count, onClick: props.onClick }));
        }
    }
}
