export { MessageObject } from './message-object';
export * from './cast';
export * from './element-listener';
export const MAIN_MENU_LABEL = 'Haskell IDE';
export function getEventType(detail) {
    if (detail &&
        (detail.contextCommand || (detail[0] && detail[0].contextCommand))) {
        return "context";
    }
    else {
        return "keyboard";
    }
}
export function bufferPositionFromMouseEvent(editor, event) {
    const sp = atom.views.getView(editor).component.screenPositionForMouseEvent(event);
    if (isNaN(sp.row) || isNaN(sp.column)) {
        return undefined;
    }
    return editor.bufferPositionForScreenPosition(sp);
}
export function handlePromise(promise) {
    if (typeof promise.catch !== 'function') {
        atom.notifications.addFatalError('Atom-Typescript: non-promise passed to handlePromise. Please report this.', {
            stack: new Error().stack,
            dismissable: true,
        });
        return;
    }
    promise.catch((err) => {
        atom.notifications.addFatalError(`Atom-Typescript error: ${err.message}`, {
            detail: err.toString(),
            stack: err.stack,
            dismissable: true,
        });
    });
}
