import * as etch from 'etch';
import { handlePromise } from '../utils';
export class TooltipMessage {
    constructor(source, message, showActions, actions) {
        this.source = source;
        this.showActions = showActions;
        if (Array.isArray(message)) {
            this.message = message.map((m) => etch.dom("div", { key: m, innerHTML: m.toHtml() }));
        }
        else {
            this.message = [etch.dom("div", { key: message, innerHTML: message.toHtml() })];
        }
        if (actions) {
            actions()
                .then(async (acts) => {
                this.actions = acts;
                return this.update();
            })
                .catch((e) => {
                console.error(e);
                atom.notifications.addError(e.name, {
                    stack: e.stack,
                    detail: e.message,
                });
            });
        }
        etch.initialize(this);
    }
    render() {
        return (etch.dom("ide-haskell-tooltip-with-actions", null,
            etch.dom("ide-haskell-tooltip", { dataset: { source: this.source } }, this.message),
            this.renderActions()));
    }
    async update() {
        return etch.update(this);
    }
    writeAfterUpdate() {
        if (this.element.parentElement) {
            this.element.parentElement.classList.add('ide-haskell');
        }
    }
    renderActions() {
        if (!this.actions)
            return null;
        if (!this.actions.length)
            return null;
        const maxActions = 8;
        return (etch.dom("ide-haskell-tooltip-actions", null,
            this.actions.slice(0, maxActions).map((act) => (etch.dom("button", { className: "btn btn-xs", on: { click: () => handlePromise(act.apply()) } }, act.title))),
            this.actions.length > maxActions ? (etch.dom("button", { className: "btn btn-xs", on: { click: this.showActions } }, "\u2026")) :
                null));
    }
}
