export function isDock(object) {
    return object.constructor.name === 'Dock';
}
export function isSimpleControlDef(def) {
    return typeof def.element === 'string';
}
export function notUndefined(val) {
    return val !== undefined;
}
export const eventRangeTypeVals = [
    "context",
    "keyboard",
    "mouse",
    "selection",
];
export function isTEventRangeType(x) {
    return (typeof x === 'string' && eventRangeTypeVals.includes(x));
}
export function isTextMessage(msg) {
    return !!(msg && msg.text);
}
export function isHTMLMessage(msg) {
    return !!(msg && msg.html);
}
