import * as etch from 'etch';
import { handlePromise } from '../utils';
export class StatusBarView {
    constructor(panel, bsc) {
        this.panel = panel;
        this.bsc = bsc;
        this.didClick = () => {
            handlePromise(this.panel.toggle());
        };
        etch.initialize(this);
        this.disp = bsc.onDidUpdate(() => {
            handlePromise(this.update());
        });
    }
    render() {
        return (etch.dom("div", { class: "ide-haskell inline-block", on: { click: this.didClick } },
            etch.dom("span", null,
                etch.dom("ide-haskell-lambda", null),
                this.bsc.renderStatusIcon())));
    }
    async update() {
        return etch.update(this);
    }
    destroy() {
        this.disp.dispose();
        handlePromise(etch.destroy(this));
    }
}
